* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  text-align: center;
  border-color: #090c09da;
  border-radius: 5px;
  margin-left: 20px;
  padding: 5px;
  font-size: 26px;
}

button[data-start] {
  height: 45px;
  width: 100px;
  letter-spacing: 1px;
  cursor: pointer;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px;
  font-size: 20px;
  font-weight: 600;
}

button[data-start]:enabled {
  background-color: #b2e2b2b6;
  border: 1px solid green;
}

button:disabled {
  background-color: #ebe6e6;
}

.timer {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.field {
  margin-top: 20px;
  font-size: 40px;
}

.value {
  font-weight: 600;
}

